.Parent-container {
  width: 100%;
}
.instruction{
  display: none;
}
.Main-container {
  scroll-behavior: smooth;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/*--------------------------About Section---------------------------*/

.BodyMainDiv {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 48rem;
  background: linear-gradient(326deg, rgba(150,117,55,1) 0%, rgba(255,248,235,1) 100%);
      background-size: 100%;
}

.BodyHeading {
  font-size: 4rem;
  font-weight: bold;
  color: rgb(0, 0, 0);
}

.container {
  color: #ffffff;
  font-size: 3rem;
  font-weight: bold;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 3rem;
}

.animation {
  height: 5rem;
  width: 28rem;
  overflow: hidden;
  margin-left: 1rem;
}

.animation>div>div {
  padding: 0.2rem 1rem;
  padding-bottom: 0.5rem;
  height: 3.8rem;
  margin-bottom: 1.59rem;
  display: inline-block;
  border-radius: 5px;
}

.animation div:first-child {
  animation: text-animation 8s infinite;
}

.first div {
  background-image: linear-gradient(to right, #434343 0%, black 100%);}

.second div {
  background-image: linear-gradient(to right, #434343 0%, black 100%);}

.third div {
  background-image: linear-gradient(to right, #434343 0%, black 100%);
}

@keyframes text-animation {
  0% {
    margin-top: 0;
  }

  10% {
    margin-top: 0;
  }

  20% {
    margin-top: -5.62rem;
  }

  30% {
    margin-top: -5.62rem;
  }

  40% {
    margin-top: -11.24rem;
  }

  60% {
    margin-top: -11.24rem;
  }

  70% {
    margin-top: -5.62rem;
  }

  80% {
    margin-top: -5.62rem;
  }

  90% {
    margin-top: 0;
  }

  100% {
    margin-top: 0;
  }
}

.resumeDiv p {
  font-size: 20px;
  width: 42rem;
  margin-left: 3rem;
  margin-bottom: 3rem;
  justify-content: flex-start;
  align-items: center;
  color: #000000;
  text-align: justify;

}

/*--------------------------About Section---------------------------*/

.BodyAboutDiv {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 3rem;
  width: 50rem;
 
}

.aboutheading {
  font-size: 3rem;
  font-weight: 900;
  color: #000;
  line-height: 0%;
  margin-left: 3rem;
  margin-bottom: 0%;
  margin-top: 7rem;
}

.aboutheading span {
  color: #BA754C;
  font-size: 4rem;
}

.AboutSubDiv {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.AboutSubHeading {
  background: -webkit-linear-gradient(15deg, #7C4434 0%, #E3BB8C 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.7rem;
  font-weight: bolder;
  margin-left: 3rem;
  line-height: 0%;
  margin-top: 2rem;
}

.AboutInfo {
  font-size:20px;
  text-align: justify;
  margin-left: 3rem;
  margin-right: 3rem;
  margin-top: 0%;
}

.img{
  margin-top: 40px;
  height:600px;
}

.BodyAboutMainDiv {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 40rem;
  width: 100%;
  padding: 3rem 0px 3rem 0px;
}

.profilePhoto {
  height: 20rem;
  width: 20rem;
  border-radius: 50%;
  margin-top: 9.5rem;
  margin-left: 7rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.resumebtn {
  height: 3rem;
  width: 20rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  border-radius: 5px;
  font-family:montserrat;
  font-weight: bold;
  font-size: 1rem;
  margin-left: 3rem;
  margin-top: 1.3rem;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  background: linear-gradient(to right, #434343 0%, black 100%);
  transition: transform 0.5s ease-in-out;
}

.resumebtn:hover {
  transform: scale(1.1);
  transition: transform 0.5s ease-in-out;
  background: -webkit-linear-gradient(15deg, #7C4434 0%, #E3BB8C 100%);
  color: #fff;
  border: none;
}

/*--------------------------Skill Section---------------------------*/

.tabtitles {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
  width: 100%;
  overflow: hidden;
  padding:40px 0px 30px 0px;
  background-color: rgb(255, 250, 235); 


}

.tablinks {
  font-size: 3rem;
  font-weight: 900;
  color: #000000;
  margin: 0;
  margin-bottom: 3rem;

}

.tablinks span {
  color: #BA754C;
  font-size: 4rem;
}

@keyframes slide {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

.logos {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: border-box;
  white-space: nowrap;
}

.logos-slide {
  display: flex;
  flex-direction: row;
  align-items: center;
  animation: 50s slide infinite linear;
}

.logos-slide img {
  width: 100px;
  height: 100px;
  border-radius: 20px;
  padding: 23px;
  margin-top: 1rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  background: #fff;
}

.logos:hover .logos-slide {
  animation-play-state: paused;
}

.tools-name {
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 3rem;
}

/*--------------------------Education & Experience Section---------------------------*/

.education-experience-div {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-around;
  margin-bottom: 10rem;
  padding-top: 9rem;
}

/*--------------------------Education Section---------------------------*/
.education-div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-basis: 40%;
}

.educationHeading {
  font-size: 3rem;
  font-weight: 900;
  color: #000;
  margin: 0;
  margin-bottom: 2rem;
}

.educationHeading span {
  color: #BA754C;
  font-size: 4rem;
}


.education-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.passing-year {
  background: -webkit-linear-gradient(15deg, #7C4434 0%, #E3BB8C 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 22px;
  font-weight: 500;
  margin: 0%;
}

.passing-year span {
  color: #000;
  font-weight: bold;
  margin-left: 3rem;
}

.collge-name {
  font-size: 20px;
  text-align: justify;
}

.collge-name-zon {
  font-size: 20px;
  text-align: justify;

}

/*--------------------------Experience Section---------------------------*/

.experience-div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-basis: 40%;
}

.experience-info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}


/*--------------------------Project Section---------------------------*/
.Projectcontainer {
  padding-top: 2rem;
  width: 1519px;
  background-color: rgb(255, 250, 235); 

}

.Projectheading {
  font-size: 3rem;
  font-weight: 900;
  color: #000;
  margin: 0;
  margin-left: 5rem;
}

.Projectheading span {
  color: #BA754C;
  font-size: 4rem;
}

.work{
  display: flex;
  flex-direction: row;
}

.work-list {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  margin-bottom: 50px;
}

.work img {
  width: 400px;
  margin-left: 5rem;
  margin-right: 3.5rem;
  margin-bottom: 5rem;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  transition: transform 0.5s ease-in-out;

}

.work img:hover {
  transform: scale(1.1);
  transition: transform 0.5s ease-in-out;
}

.work2{
  display: flex;
  flex-direction:row-reverse;
}

.work2 img{
  width: 400px;
  margin-left: 3rem;
  margin-right: 3.5rem;
  margin-bottom: 5rem;
