  transition: transform 0.5s ease-in-out;

}

.work2 img:hover {
  transform: scale(1.1);
  transition: transform 0.5s ease-in-out;
}

.layer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align:left;
  padding: 20px;
  width: 1000px;
  height: 200px;
  border-radius: 5px;
  margin-left: 5rem;
  margin-right: 3.5rem;
}

.layer h2 {
