.NavbarMainDiv{
    color: #000000;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content:space-between;
    align-items: center;
    height:4rem;
position: absolute;

}

.Logo{
    font-size: 22px;
    letter-spacing:0.5px;
    font-weight: 600;
    margin: 0px 0px 10px 3rem;  
    color: #000000;
}

.Logo span{
    color: #000000;
    font-size: 40px;
}

.NavLinksDiv{
    width:40%;
    height:50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.nav-links{
    text-decoration: none;
    position: relative;
    cursor: pointer;
    font-size: 18px;
}

.nav-links::after{
    content: "";
    width:0;
    height:1px;
    background-color: #99694d;
    position:absolute;
    left:0%;
    bottom: -6px;
    transition: 0.5s;
    border-radius: 5px;
}

.nav-links:hover::after{
    width: 100%;
}

.nav-img-div{
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
}

.nav-img-div img{
    max-width: 2rem;
    max-height: 2rem;
    cursor: pointer;
    margin-right: 1rem;
}

.nav-img-div img:hover{
    transform: scale(1.3);
    transition: transform 0.5s ;
    
}

/*---------------------------- Responsive CSS------------------------- */
@media only screen and (max-width: 480px){
    .NavbarMainDiv{
        width:480px;
        height:80px;
    
    }

    .NavLinksDiv{
        display: none;
    }

    .Logo{
        font-size: 20px;
        margin-bottom: 20px;
        margin-left: 15px;
    }



}



